<div>{this.props.filter['manual_contact_tracing'] ? <AppItem    name={"NextTrace"}    homepage={"https://nexttrace.org/"}    details={`The first deliverable is a survey-based contact tracing platform that can be deployed by public health officials around the United States. Beyond simply developing a system that can monitor individual-level interactions and exposure events, understanding high resolution transmission patterns on a broad scale would allow us to characterize the heterogeneity of the epidemic in different areas. While we are all facing the same disease, variable levels of immunity, population densities, and age structures will create subtly different environments for transmission. Therefore, in addition to guiding individual-level containment measures after exposure, we will use this platform to help inform containment policies at the community level.`}    functions={"Manual Contact Tracing"}    platforms={"Web"}    is_free={"Yes"}    is_optin={"Yes"}    status={"In Development"}    en_api={"Not applicable"}    state_adoption={"United States"}     target_audience={"Healthcare Workers, Governments"}    technologies={"Decentralized"}    country_coverage={"United States"}    organizations={"[Next Strain](https://nextstrain.org)"}    people={"[Alli Black](https://twitter.com/alliblk?lang=en), [Leah Alpert](https://www.linkedin.com/in/leah-alpert/), [Silvia Botros](https://twitter.com/dbsmasher), [Rebecca Egger](https://www.linkedin.com/in/rebecca-egger-47012b51/), [Jeremy Freeman](https://www.linkedin.com/in/jeremy-freeman/), [Marc Hedlund](https://www.linkedin.com/in/precipice/), [Colin Megill](https://twitter.com/colinmegill), [Miguel Paredes](https://www.linkedin.com/in/miguel-i-paredes-b08b08124/), [Thomas Sibley](https://twitter.com/trs)"}/> : <div/>}</div>
