<div>{this.props.filter['health_passport'] ? <AppItem    name={"Covi-Id"}    homepage={"https://www.coviid.me/"}    details={`Covi-ID is a uniquely African Solution that gives everyone the ability to prove their COVID-19 status, reliable, secure, and without loss of privacy. We combine self-sovereign identity and the latest blockchain technology with extensive use of QR codes so that anyone can use Covi-ID. In addition, Covi-ID will implement a simple track and trace system even for users who dont have phones. But instead of collecting everybodys data centrally, we communicate possible infection hotspots to the users wallets once a day so that our app then checks whether there is any overlap with the locally stored geo-location history. Our goal is to provide an open-sourced, free, and privacy-preserving platform because we believe that this is the most efficient way of achieving positive public health effects and enhance our public health infrastructure. Consequently, Covi-ID will be offered free of charge to anyone.`}    functions={"Health Passport"}    platforms={"iOS, Android, Web"}    is_free={"Yes"}    is_optin={"Yes"}    status={"In Development"}    en_api={"Planned"}    state_adoption={"South Africa"}     target_audience={"General Public"}    technologies={"Open Source, QR, Blockchain, SSI"}    country_coverage={"South Africa"}    organizations={"Data Unavailable"}    people={"Co-Pierre Georg"}/> : <div/>}</div>
